.flex_center {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.image {
  position: relative;
  height: 300px;
  width: 550px;
  border-radius: 5px;
  background: no-repeat center / cover
    url('https://images.unsplash.com/photo-1611647832580-377268dba7cb?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=2550&q=80');
  left: 100px;
}

.image:after {
  content: '';
  height: 100%;
  width: 100%;
  position: absolute;
  background: #6f73d2;
  opacity: 0.15;
  border-radius: 5px;
}

.lead {
  font-size: 1.5em;
  letter-spacing: 5px;
  position: relative;
  top: -50px;
  left: -150px;
}

.name {
  display: inline-block;
  transition: color 0.5s ease-in-out;
}
.name:hover {
  color: #f4e76e;
}
